.container {
    display: block;
    position: relative;
    padding-left: 30px;
    cursor: pointer;
    font-size: 19px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    color: white;
    margin: 13px 45px;
}

.container:hover {
    color: #ccc;
}

.container input {
    position: absolute;
    opacity: 0;
}

.container:hover input ~ .checkmark {
    background: #ccc -webkit-gradient(linear, left top, left bottom, from(#4c4c4c), to(#ccc));
    background: #ccc -webkit-linear-gradient(top, #4c4c4c, #ccc);
    background: #ccc -o-linear-gradient(top, #4c4c4c, #ccc);
    background: #ccc linear-gradient(to bottom, #4c4c4c, #ccc);
}
.container input:checked ~ .checkmark {
    background: #2196F3 -webkit-gradient(linear, left bottom, left top, from(rgba(5, 132, 242, 1)), to(rgba(10, 175, 241, 1)));
    background: #2196F3 -webkit-linear-gradient(bottom, rgba(5, 132, 242, 1), rgba(10, 175, 241, 1));
    background: #2196F3 -o-linear-gradient(bottom, rgba(5, 132, 242, 1), rgba(10, 175, 241, 1));
    background: #2196F3 linear-gradient(to top, rgba(5, 132, 242, 1), rgba(10, 175, 241, 1));
}

.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 22px;
    width: 22px;
    background-color: #eee;
    border-radius: 50%;
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

.container input:checked ~ .checkmark:after {
    display: block;
}

.container .checkmark:after {
    top: 8px;
    left: 8px;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: white;
}

@media screen and (max-height: 500px) {
    .container {
        font-size:13px;
        margin: 8px 25px;
    }

    .checkmark {
        height: 15px;
        width: 15px;
    }

    .container .checkmark:after {
        top: 5px;
        left: 5px;
        width: 5px;
        height: 5px;
    }
}


