.menu {
    position: fixed;
    float: right;
    right: 0;
    height: 80px;
    width: 80px;
    cursor: pointer;
    -webkit-transition: 0.1s;
    -o-transition: 0.1s;
    transition: 0.1s;
    z-index: 501;
}

.menu:hover {
    background: -webkit-gradient(linear, left top, right top, from(rgba(244, 86, 66, 0)), to(rgba(76, 217, 100, 0.4)));
    background: -webkit-linear-gradient(left, rgba(244, 86, 66, 0), rgba(76, 217, 100, 0.4));
    background: -o-linear-gradient(left, rgba(244, 86, 66, 0), rgba(76, 217, 100, 0.4));
    background: linear-gradient(to right, rgba(244, 86, 66, 0), rgba(76, 217, 100, 0.4));
}
    .menu:hover .bar {
        -webkit-box-shadow: 2px 2px 5px 0 #222222;
                box-shadow: 2px 2px 5px 0 #222222;
        background: -webkit-gradient(linear, left top, left bottom, from(white), color-stop(white), to(gray));
        background: -webkit-linear-gradient(top, white, white, gray);
        background: -o-linear-gradient(top, white, white, gray);
        background: linear-gradient(to bottom, white, white, gray);
    }

.bar {
    position: relative;
    width: 30px;
    height: 3px;
    background-color: white;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
    top: 25%;
    display: block;
    margin: 8px auto;
}

.change #bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 8px);
        -ms-transform: rotate(-45deg) translate(-9px, 8px);
            transform: rotate(-45deg) translate(-9px, 8px);
}
.change #bar2 {
    opacity: 0;
}
.change #bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
        -ms-transform: rotate(45deg) translate(-8px, -8px);
            transform: rotate(45deg) translate(-8px, -8px);
}

.change:hover .bar {;
    margin: 8px auto;
    width: 30px;
    top: 25%;
    -webkit-box-shadow: none;
            box-shadow: none;
}


