.contentContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: stretch;
        align-items: stretch;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}

.picContainer {
    -ms-flex: 30% 1;
        flex: 30% 1;
    margin: 10px;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}

.audio {
    margin-top: 20px;
    max-width: 420px;
}

.textContainer {
    -ms-flex: 70% 1;
        flex: 70% 1;
    height: auto;
    border-radius: 30px;
    margin: 10px;
    background: #eee -webkit-gradient(linear, left top, right bottom, from(#f2f2f2), to(#dddddd));
    background: #eee -webkit-linear-gradient(top left, #f2f2f2, #dddddd);
    background: #eee -o-linear-gradient(top left, #f2f2f2, #dddddd);
    background: #eee linear-gradient(to bottom right, #f2f2f2, #dddddd);
    display: -ms-flexbox;
    display: flex;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}

.image {
    max-width: 420px;
    max-height: 420px;
    background: #eee -webkit-gradient(linear, left top, right bottom, from(#f2f2f2), to(#dddddd));
    background: #eee -webkit-linear-gradient(top left, #f2f2f2, #dddddd);
    background: #eee -o-linear-gradient(top left, #f2f2f2, #dddddd);
    background: #eee linear-gradient(to bottom right, #f2f2f2, #dddddd);
    border-radius: 30px;
    padding: 15px;
}


/* Når størrelsen er små tablets/store telefoner synker tekst under bilde*/
@media screen and (max-width: 767px) {
    .contentContainer {
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: center;
            align-items: center;
    }

    .image {
        width: 250px;
        height: 250px;
    }

    .audio {
        width: 283px;
    }
}

pre {
    font-family: Garamond, Georgia, Verdana, serif;
    white-space: pre-line;
    text-align: center;
    font-size: 20px;
    padding: 10px 0;
    margin: auto;
}

blockquote {
    font-size: 1.2em;
    line-height: 1.5em;
}

blockquote:before {
    content: open-quote;
}
blockquote:after {
    content: close-quote;
}

blockquote:before, blockquote:after {
    display: inline-block;
    vertical-align: bottom;
    font-size: 4em;
    top: .2em;
    padding: 0 10px;
    position: relative;
}


